}

/* reset end */
/* browserupgrade */
.old-ie {
  overflow: hidden;
}

.old-ie body {
  overflow: hidden;
}

.browserupgrade {
  position: fixed;
  height: 100%;
  width: 100%;
  padding: 0.2em 0;
  margin: 0;
  font-size: 28px;
  line-height: 42px;
  color: black;
  background: #cccccc;
  z-index: 99999;
}

.browserupgrade p {
  position: absolute;
  width: 100%;
  top: 50%;
  margin: -22px 0 0;
  padding: 0;
  text-align: center;
  vertical-align: middle;
}

/* browserupgrade end */
/* no js warning */
.no-js-warning {
  border: 2px solid red;
  color: red;
  background: white;
  padding: 5px 20px;
  font-size: 18px;
}

