body.home-page::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 77.5rem;
  background: #644435 url(../i/main-bg.jpg) no-repeat 50%;
  background-size: cover;
  opacity: 0.6;
}

@media (max-width: 991px) {
  body.home-page::before {
    height: auto;
    padding-top: calc(47% + 19rem);
  }
}

@media (max-width: 639px) {
  body.home-page::before {
    height: auto;
    padding-top: calc(47% + 14.9rem);
  }
}

body.home-page::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 77.5rem;
  background: #644435;
  background: radial-gradient(ellipse at center, #d08d74 1%, #d08d74 12%, #644435 47%, #644435 100%);
  z-index: -1;
}

@media (max-width: 991px) {
  body.home-page::after {
    height: auto;
    padding-top: calc(47% + 19rem);
  }
}

@media (max-width: 639px) {
  body.home-page::after {
    height: auto;
    padding-top: calc(47% + 14.9rem);
  }
}

