/* button toggle menu */
.btn-menu {
  display: none;
  width: 5rem;
  height: 5rem;
  position: fixed;
  left: 0;
  top: 0;
  text-decoration: none;
  color: #e7dad2;
  background-color: transparent;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  z-index: 99;
  transition-property: background-color;
  transition-duration: 0.2s;
}

@media (max-width: 991px) {
  .btn-menu {
    display: block;
  }
}

.touchevents .btn-menu {
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.home-page .btn-menu {
  color: #403b38;
}

.btn-menu > span {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: normal;
}

.btn-menu > span:active, .btn-menu > span:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

.btn-menu::before, .btn-menu::after {
  content: "";
  display: block;
  position: absolute;
  width: 24px;
  height: 3px;
  background-color: #e7dad2;
  top: 50%;
  left: 50%;
  margin-top: -2px;
  margin-left: -12px;
  will-change: opacity, transform;
  opacity: 0;
  transition-property: transform, opacity;
  transition-duration: 0.2s;
  transition-delay: 0s;
  transition-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
}

.home-page .btn-menu::before, .home-page .btn-menu::after {
  background-color: #403b38;
}

.btn-menu > i {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 13px;
  height: 3px;
  background-color: currentColor;
  margin-top: -2px;
  margin-left: -12px;
  border-radius: 3px;
  backface-visibility: hidden;
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-delay: 0.1s;
}

.btn-menu > i::before, .btn-menu > i::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  width: 24px;
  height: 3px;
  border-radius: 3px;
  background-color: currentColor;
  backface-visibility: hidden;
  transition-property: transform;
  transition-duration: 0.2s;
  transition-delay: 0.1s;
}

.btn-menu > i::before {
  top: -8px;
}

.btn-menu > i::after {
  transform: scale(0.75, 1);
  transform-origin: left center;
  top: 8px;
}

.no-touchevents .btn-menu:hover {
  box-shadow: inset 0 -2px 0 #c4161c;
}

.btn-menu.active::before, .btn-menu.active::after {
  transition-delay: 0.1s;
  opacity: 1;
}

.btn-menu.active::before {
  transform: rotate(45deg) translateZ(0);
}

.btn-menu.active::after {
  transform: rotate(-45deg) translateZ(0);
}

.btn-menu.active > i {
  opacity: 0;
  transition-delay: 0s;
}

.btn-menu.active > i::before, .btn-menu.active > i::after {
  transition-delay: 0s;
}

.btn-menu.active > i::before {
  transform: translate(0, 8px) translateZ(0);
}

.btn-menu.active > i::after {
  transform: translate(0, -8px) translateZ(0);
}

