}

.swiper-pagination-bullet::before, .swiper-pagination-bullet::after {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  background-color: #c4161c;
}

.swiper-pagination-bullet::before {
  height: 1.2rem;
  width: 1.2rem;
  margin: -0.6rem 0 0 -0.6rem;
  border-radius: 1.2rem;
  transition: opacity 0.2s, transform 0.2s;
  opacity: 0.5;
}

