  }
}

.swiper-pagination-bullet::after {
  height: 1.6rem;
  width: 1.6rem;
  margin: -0.8rem 0 0 -0.8rem;
  border-radius: 1.6rem;
  background: #fe0011;
  background: -webkit-linear-gradient(top, #fe0011 0%, #db0009 100%);
  background: linear-gradient(to bottom, #fe0011 0%, #db0009 100%);
  border: 2px solid #feea95;
  box-shadow: 0 0 0 1px #d0a25a, 0 0 5px #feea95;
  transition: opacity 0.2s, transform 0.2s;
  opacity: 0;
  transform: scale(0.5, 0.5);
  transform: scale3d(0.5, 0.5, 1);
}

@media (max-width: 639px) {
  .swiper-pagination-bullet::after {
    height: 1rem;
    width: 1rem;
    margin: -0.8rem 0 0 -0.8rem;
